<template>
  <div class="more">
    <h2>司機詳細資料</h2>
    <div class="more-d" v-for="(item,index) in list_key">{{item}}: {{list[item]}}<hr></div>
  </div>
</template>

<script>
import VueRouter from 'vue-router'
import axios from 'axios'
import m from './../all.js'
export default {
  data() {
    return {
	  list:[],
	  list_key:[]
	}
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data(this.$route.params.id)
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.id)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  this.list=await m.get_driver_more(x)
	  this.list_key=Object.keys(this.list)
	  console.log(this.list)
	}
  }
}
</script>



