// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import "@babel/polyfill";
import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App'
import Top from './Top'
import Left from './Left'
import List from './List'
import Global from './components/Global'
import Black from './components/Black'
import CarList from './components/CarList'
import CaseList from './components/CaseList'
import DriverList from './components/DriverList'
import CaseMore from './components/CaseMore'
import CarMore from './components/CarMore'
import DriverMore from './components/DriverMore'
import DragDispatch from './components/DragDispatch'
import './css/index.css'
import m from './all.js'

Vue.config.productionTip = false
Vue.prototype.GLOBAL = Vue.observable(Global)
Vue.prototype.all_ready=async function (){
  while(!this.GLOBAL.ready) {
	await m.delay(1)
  }  
}
Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    { path: '/Left', component: App },
	{ path: '/Left/:id', component: List },
	{ path: '/CaseList/:page', component: CaseList },
	{ path: '/DriverList/:page', component: DriverList },
	{ path: '/CarList/:page', component: CarList },
	{ path: '/CaseMore/:id', component: CaseMore },
	{ path: '/CarMore/:id', component: CarMore },
	{ path: '/DriverMore/:id', component: DriverMore },
	{ path: '/DragDispatch', component: DragDispatch },
  ]
})
/* eslint-disable no-new */
new Vue({
  el: '#main',
  router,
  data() {
    return {
	  d1:"123",
	}
  },
  watch:{
    $route (to, from){
      //location.reload()
    }
  },
  created:async function() {
	var token=await m.get_token()
	await m.put_token(token.token)
	this.GLOBAL.ready=true
	console.log(token)
	//await m.delay(1000)
  },
  components: { App, Top ,Left ,Black},
  template: '<div id="main">\
    <Black v-if="GLOBAL.show_black"/>\
    <Top/>\
	<div class="d1">\
	  <Left/>\
	  <div class="d1-r">\
	    <router-view/>\
	  </div>\
	</div>\
	</div>\
  </div>'
})



