<template>
  <div class="page">
    <router-link v-for="(item,index) in max" :to="url+item">
      <div class="page-d">{{item}}</div>
	</router-link>
  </div>
</template>

<script>
import m from './../all.js'
export default {
  props: ['max','url'],
  data() {
    return {
	  list:[],
	}
  },
  mounted:async function() {
    this.sel_page()
  },
  watch:{
    $route (to, from){
      this.sel_page()
    }
  },
  methods:{
    sel_page:function() {
	  this.list=[]
	  var num=Number(this.$route.params.page)
	  var arr=[num-3,num-2,num-1,num,num+1,num+2,num+3]
	  for(let i in arr) {
	    if(arr[i]>0&&arr[i]<=this.max) {
		  this.list.push(arr[i])
		}
	  }
	}
  }
}
</script>



