import axios from 'axios'

var allf={
  delay:function(x) {  //延遲
    return new Promise(function (resolve) {
      setTimeout(() => {
	    resolve();
      }, x);
    });	  
  },
  get_token:async function() {  //取得token
	return await axios.get("https://api.donkeymove.com/api/CompanyUser/GetJwtToken/api/CompanyJWTToken?name=comp01&pass=1qaz%40WSX")
	.then(res=>{
      return res.data
	})
  },
  put_token:async function(x) {
    await axios.interceptors.request.use(
      config => {
        config.headers.Authorization ="Bearer "+x;
		console.log("已放置token")
		//window.location.reload()
        return config;
    });  	  
  },
  get_data:async function() {  //測試用
	return await axios.get("http://www.e9life.com/api/AddrTurnName/Get?page=1")
	.then(res=>{
      return res.data.response.data
	})
  },
  get_case_user:async function(x) {  //取得個案-分頁
	return await axios.get("https://api.donkeymove.com/api/CaseUser/Get?page="+x+"&key=&isDescending=true&pMode=1")
	.then(res=>{
      return res.data.response
	})
  },
  get_case_more:async function(x) {  //取得個案詳細資料
	return await axios.get("https://api.donkeymove.com/api/CaseUser/GetUsersByCId/"+x)
	.then(res=>{
      return res.data.response
	})
  },
  get_driver:async function(x) {  //取得司機-分頁
	return await axios.get("https://api.donkeymove.com/api/DriverInfo/Get?page="+x+"&isDescending=false")
	.then(res=>{
      return res.data.response
	})
  },
  get_driver_more:async function(x) {  //取得司機-分頁
	return await axios.get("https://api.donkeymove.com/api/DriverInfo/Get/"+x)
	.then(res=>{
      return res.data.response
	})
  },
  get_car:async function(x) {  //取得車輛-分頁
	return await axios.get("https://api.donkeymove.com/api/Cars/Get?page="+x+"&carType=0&isDescending=false")
	.then(res=>{
      return res.data.response
	})
  },
  get_car_more:async function(x) {  //取得車輛詳細資料
	return await axios.get("https://api.donkeymove.com/api/Cars/Get/"+x)
	.then(res=>{
      return res.data.response
	})
  },
  get_dispatch_order:async function() {  //取得可排班的訂單
	return await axios.get("https://api.donkeymove.com/api/OrderDetails/GeOrderDetailtForDespatch/2020-01-08")
	.then(res=>{
      return res.data.response
	})
  },
  get_dispatch_allgroup:async function() {  //取得已排班的派遣單
	return await axios.get("https://api.donkeymove.com/api/Despatch/GetAllGroup?key=2020-01-08")
	.then(res=>{
      return res.data.response
	})
  },
  get_dispatch_car:async function() {  //取得可派遣的車輛(含司機資料)
	return await axios.get("https://api.donkeymove.com/api/Cars/GetCarsWithCarTypeNoPage?carType=0&isDescending=false")
	.then(res=>{
      return res.data.response
	})
  },
  reload:function() {
	alert(123)
  }
}
export default allf


