<template>
  <div class="list">
    <h2>個案管理</h2>
    <router-link v-for="(item,index) in list.data" :to="'/CaseMore/'+item.Id">
      <div class="list-d">{{item.Name}}</div>
	</router-link>
	<Page :max="list.pageCount" :url="/CaseList/"/>
  </div>
</template>

<script>
import Page from './Page'
import VueRouter from 'vue-router'
import axios from 'axios'
import m from './../all.js'
export default {
  data() {
    return {
	  list:[],
	}
  },
  components: {Page},
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data(this.$route.params.page)
	console.log(this.list)
	this.GLOBAL.show_black=false
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.page)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  this.list=await m.get_case_user(x)
	  console.log(this.list)
	}
  }
}
</script>



